import React from "react";

import Firebase from "firebase";
import config from "./config";
import { Line } from "react-chartjs-2";

class App extends React.Component {
  constructor(props) {
    super(props);
    Firebase.initializeApp(config);

    this.state = {
      data: [],

      time:[],
      moist:[],

      data2: {
       
      },
    };
  }

  componentDidMount() {
    this.getUserData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState !== this.state) {
    }
  }

  getUserData = () => {
    let ref = Firebase.database().ref("/");
    ref.on("value", (snapshot) => {
      const state = snapshot.val();

      // console.log(state);
      const tempData = [
        [state.Live_Moisture, state.Live_Time],
        ...this.state.data,
      ];

      const m2 = [...this.state.moist,state.Live_Moisture];
      const t2 = [...this.state.time,state.Live_Time];
      

      

      const tempData2 = {
        labels: t2,
        datasets: [
          {
            label: "Home Plant",
            fill: false,
            
            lineTension: 0.1,
            backgroundColor: "rgba(75,192,192,0.4)",
            borderColor: "rgba(75,192,192,1)",
            borderCapStyle: "round",
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: "miter",
            pointBorderColor: "rgba(75,192,192,1)",
            pointBackgroundColor: "#fff",
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            
            pointHoverBackgroundColor: "rgba(75,192,192,1)",
            pointHoverBorderColor: "rgba(220,220,220,1)",
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: m2,
            responsive: true,
  maintainAspectRatio: false,
  scales: {
    xAxes: [{
        ticks: {
            autoSkip: false,
            maxRotation: 90,
            minRotation: 90
        }
    }]
}
          },
        ],
      };

      if (tempData.length > 20) {
        tempData.pop();
        // time.pop();
        // moist.pop();
        t2.shift();
        m2.shift();
      }
      this.setState({
        data: tempData,
        time:t2,
        moist:m2,

        
        data2:tempData2
        
      });

      // this.setState(state);
    });
  };

  render() {
    return (
      <React.Fragment>
        <div >
        <div class="jumbotron text-center">
        <h1>HomePlant</h1>
        <p>Live Soil Moisture data</p>
    </div>
    <Line ref="chart" data={this.state.data2} />

    
            


          
            

    
      
        
          <div className="container-lg">
            

           
            
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
